*
{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
button,
input,
optgroup,
select,
textarea {
    color: inherit;
    font: inherit;
    margin: 0;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}
button[disabled],
html input[disabled] {
    cursor: not-allowed;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}
input {
    line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    border-width: 0;
}
input::-ms-clear {
    display: none;
}
input[type="file"]::-ms-browse,
input[type="file"]::-webkit-file-upload-button {
    border-width: 0;
    background: transparent;
    color: currentColor;
}
button,
input,
select,
textarea {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}
.rs-auto-complete .rs-input:focus::-webkit-textfield-decoration-container {
    visibility: hidden;
    pointer-events: none;
}

a:active,
a:hover {
    outline: 0;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
}


body {
    font-size: 0.882rem;
    color: #575757;
    background-color: #fff;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
[hidden],
template {
    display: none;
}
a {
    background-color: transparent;
}

b,
strong {
    font-weight: 400;
}
sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}
sup {
    top: -0.024rem;
}
sub {
    bottom: -0.012rem;
}
img {
    border: 0;
}
svg:not(:root) {
    overflow: hidden;
}
hr {
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    height: 0;
}
pre {
    overflow: auto;
}

button {
    overflow: visible;
    border-radius: 0;
}
button,
select {
    text-transform: none;
}
textarea {
    overflow: auto;
}

td,
th {
    padding: 0;
}
[tabindex="-1"] {
    outline: none;
}
.clearfix:after,
.clearfix:before {
    content: " ";
    display: table;
}
.clearfix:after {
    clear: both;
}
.pull-right {
    float: right !important;
}
.pull-left {
    float: left !important;
}
.hide {
    display: none !important;
}
.show {
    display: block !important;
}
.invisible {
    visibility: hidden;
}
.hidden {
    display: none !important;
}
.fade {
    opacity: 0;
    -webkit-transition: opacity 0.15s linear;
    transition: opacity 0.15s linear;
    pointer-events: none;
}
.fade.in {
    opacity: 1;
    pointer-events: unset;
}
.collapse {
    display: none;
}
.collapse.in {
    display: block;
}
tr.collapse.in {
    display: table-row;
}
tbody.collapse.in {
    display: table-row-group;
}
.collapsing {
    position: relative;
    height: 0;
    overflow: hidden;
    -webkit-transition: height 0.35s ease, visibility 0.35s ease;
    transition: height 0.35s ease, visibility 0.35s ease;
}
.sr-only {
    position: absolute;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}
